"use client"

import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Smartphone } from "lucide-react"
import { useState, useEffect } from "react"
import { useAuth } from "@/contexts/auth-context"
import { useData } from "@/contexts/data-context"
import { useToast } from "@/hooks/use-toast"
import { useLanguage } from "@/contexts/language-context"

type PaymentMethod = "crystalpay"

function PaymentMethods() {
  const [amount, setAmount] = useState("")
  const { user, refresh } = useAuth()
  const { addTransaction, refreshData } = useData()
  const { toast } = useToast()
  const [cpEnabled, setCpEnabled] = useState<boolean>(false)
  const [balance, setBalance] = useState<number>(0)
  const { language } = useLanguage()

  const paymentOptions = [
    {
      id: "crystalpay" as PaymentMethod,
      name: language === "ru" ? "CrystalPay" : "CrystalPay",
      icon: Smartphone,
      description: language === "ru" ? "" : ".",
      fee: language === "ru" ? "–¥–æ 5%" : "up to 5%",
    },
  ]

  useEffect(() => {
    if (typeof window !== "undefined") {
      if (!cpEnabled) {
        fetch("/api/config/payments", { cache: "no-store" })
          .then((r) => r.json())
          .then((cfg) => setCpEnabled(Boolean(cfg?.crystalpayEnabled)))
          .catch(() => setCpEnabled(false))
      }

      if (user && balance === 0) {
        fetch("/api/profile", { cache: "no-store" })
          .then((r) => (r.ok ? r.json() : null))
          .then((p) => setBalance(typeof p?.balance === "number" ? p.balance : 0))
          .catch(() => setBalance(0))
      }

      // –ü—Ä–æ–≤–µ—Ä—è–µ–º –≤–æ–∑–≤—Ä–∞—Ç —Å –ø–ª–∞—Ç–µ–∂–Ω–æ–π —Å–∏—Å—Ç–µ–º—ã
      const params = new URLSearchParams(window.location.search)
      const id = params.get('id')
      const state = params.get('state')

      // –ï—Å–ª–∏ –µ—Å—Ç—å id –∏–Ω–≤–æ–π—Å–∞ –∏ —Å–æ—Å—Ç–æ—è–Ω–∏–µ –æ–ø–ª–∞—á–µ–Ω–æ - –ø—Ä–æ–≤–µ—Ä—è–µ–º –ø–ª–∞—Ç–µ–∂
      if (id && (state === 'payed' || state === 'paid')) {
        confirmCrystalPayment(id)
      } else if (id) {
        // –ï—Å–ª–∏ –µ—Å—Ç—å id –Ω–æ —Å—Ç–∞—Ç—É—Å –Ω–µ –æ–ø–ª–∞—á–µ–Ω - —Ç–æ–∂–µ –ø—Ä–æ–≤–µ—Ä—è–µ–º (–Ω–∞ —Å–ª—É—á–∞–π –µ—Å–ª–∏ —Å—Ç–∞—Ç—É—Å –Ω–µ –ø–µ—Ä–µ–¥–∞–ª—Å—è)
        confirmCrystalPayment(id)
      }
    }
  }, [user, cpEnabled, balance])

  const confirmCrystalPayment = async (id: string) => {
    try {
      toast({
        title: language === "ru" ? "–ü—Ä–æ–≤–µ—Ä–∫–∞ –ø–ª–∞—Ç–µ–∂–∞..." : "Verifying payment...",
      })

      const response = await fetch('/api/crystalpay/confirm', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ id }),
      })

      const data = await response.json()

      if (!response.ok) {
        throw new Error(data.error || 'Confirmation failed')
      }

      if (data.paid) {
        toast({
          title: language === "ru" ? "–ü–ª–∞—Ç–µ–∂ —É—Å–ø–µ—à–µ–Ω!" : "Payment successful!",
          description: language === "ru" 
            ? `–ë–∞–ª–∞–Ω—Å –ø–æ–ø–æ–ª–Ω–µ–Ω –Ω–∞ $${data.amount || '0'}` 
            : `Balance topped up with $${data.amount || '0'}`,
        })

        // –û–±–Ω–æ–≤–ª—è–µ–º –±–∞–ª–∞–Ω—Å –∏ –¥–∞–Ω–Ω—ã–µ
        await refresh()
        refreshData()
        
        // –û–±–Ω–æ–≤–ª—è–µ–º –ª–æ–∫–∞–ª—å–Ω—ã–π –±–∞–ª–∞–Ω—Å
        const profileRes = await fetch("/api/profile", { cache: "no-store" })
        if (profileRes.ok) {
          const profile = await profileRes.json()
          setBalance(profile.balance || 0)
        }

        // –û—á–∏—â–∞–µ–º –ø–∞—Ä–∞–º–µ—Ç—Ä—ã –∏–∑ URL
        const url = new URL(window.location.href)
        url.searchParams.delete("id")
        url.searchParams.delete("state")
        url.searchParams.delete("tx")
        window.history.replaceState({}, "", url.toString())
      } else {
        toast({
          title: language === "ru" ? "–û–∂–∏–¥–∞–Ω–∏–µ –æ–ø–ª–∞—Ç—ã" : "Waiting for payment",
          description: language === "ru" 
            ? "–ü–ª–∞—Ç–µ–∂ –µ—â–µ –Ω–µ –æ–±—Ä–∞–±–æ—Ç–∞–Ω" 
            : "Payment not yet processed",
        })
      }
    } catch (err) {
      toast({
        title: language === "ru" ? "–û—à–∏–±–∫–∞" : "Error",
        description: (err as Error).message,
        variant: "destructive",
      })
    }
  }

  const handlePayment = async (method: "crystalpay" | "lzt") => {
    if (!user || !amount || Number.parseFloat(amount) < 10) {
      toast({
        title: language === "ru" ? "–û—à–∏–±–∫–∞" : "Error",
        description: language === "ru" ? "–ú–∏–Ω–∏–º–∞–ª—å–Ω–∞—è —Å—É–º–º–∞: $10 USD" : "Minimum amount: $10 USD",
        variant: "destructive",
      });
      return;
    }

    let roundedAmount = Number.parseFloat(amount);
    if (isNaN(roundedAmount) || roundedAmount <= 0) {
      toast({
        title: language === "ru" ? "–û—à–∏–±–∫–∞" : "Error",
        description: language === "ru" ? "–ù–µ–∫–æ—Ä—Ä–µ–∫—Ç–Ω–∞—è —Å—É–º–º–∞ (—Ç–æ–ª—å–∫–æ USD!)" : "Invalid amount (USD only)",
        variant: "destructive",
      });
      return;
    }
    roundedAmount = Number(roundedAmount.toFixed(2));

    let sendAmount = roundedAmount
    if (method === 'lzt') {
      sendAmount = Number((roundedAmount * 1.05).toFixed(2)) // –∏—Å–ø—Ä–∞–≤–ª–µ–Ω–æ: –æ–∫—Ä—É–≥–ª–µ–Ω–∏–µ —Å —É—á–µ—Ç–æ–º –∫–æ–º–∏—Å—Å–∏–∏
    }

    try {
      toast({
        title: `${method === "crystalpay" ? "CrystalPay" : "LZT"}: ${language === "ru" ? "–æ–±—Ä–∞–±–æ—Ç–∫–∞ –ø–ª–∞—Ç–µ–∂–∞..." : "processing..."}`,
        description: language === "ru" ? "–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –ø–æ–¥–æ–∂–¥–∏—Ç–µ" : "Please wait",
      });

      const endpoint = method === "crystalpay" ? "/api/crystalpay" : "/api/lzt/create"
      const response = await fetch(endpoint, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          amount: sendAmount,
          description: language === "ru" ? `–ü–æ–ø–æ–ª–Ω–µ–Ω–∏–µ —á–µ—Ä–µ–∑ ${method === "crystalpay" ? "CrystalPay" : "LZT"} ` : `Top-up via ${method === "crystalpay" ? "CrystalPay" : "LZT"} (USD)`,
          currency: "USD",
          userId: user.id,
          lifetime: 1440,
        }),
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.error || "Unknown error");
      }

      // –∏—Å–ø—Ä–∞–≤–ª–µ–Ω–æ: –¥–æ–±–∞–≤–ª–µ–Ω type (topup), currency, –∏ method –¥–ª—è addTransaction
      addTransaction({
        userId: user.id,
        amount: roundedAmount,
        currency: "USD",
        status: "pending",
        method: method,
        description:
          method === "crystalpay"
            ? language === "ru"
              ? `–ü–æ–ø–æ–ª–Ω–µ–Ω–∏–µ —á–µ—Ä–µ–∑ CrystalPay`
              : `Top-up via CrystalPay`
            : language === "ru"
            ? `–ü–æ–ø–æ–ª–Ω–µ–Ω–∏–µ —á–µ—Ä–µ–∑ LZT`
            : `Top-up via LZT`,
        type: "topup",
      });

      toast({
        title: language === "ru" ? "–ò–Ω–≤–æ–π—Å —Å–æ–∑–¥–∞–Ω!" : "Invoice created!",
        description: language === "ru"
          ? `–ü–µ—Ä–µ–Ω–∞–ø—Ä–∞–≤–ª—è–µ–º –Ω–∞ –æ–ø–ª–∞—Ç—É ${method === "crystalpay" ? "CrystalPay" : "LZT"}...`
          : `Redirecting to ${method === "crystalpay" ? "CrystalPay" : "LZT"}...`,
      });

      window.location.href = data.url;
    } catch (err) {
      toast({
        title: language === "ru" ? "–û—à–∏–±–∫–∞" : "Error",
        description: (err as Error).message,
        variant: "destructive",
      });
    }
  };

  const amt = Number.parseFloat(amount || "0")
  const totalCrystal = amt >= 10 ? `$${amt.toFixed(2)}` : "0.00"
  const lztWithFee = amt >= 10 ? amt * 1.05 : 0
  const totalLzt = amt >= 10
    ? `$${lztWithFee.toFixed(2)}`
    : "0.00"

  return (
    <div className="card-gradient rounded-xl p-6 lg:p-8 border border-border/50">
      <div className="flex items-center justify-between mb-6">
        <h2 className="text-2xl font-bold text-foreground">
          {language === "ru" ? "–ü–æ–ø–æ–ª–Ω–∏—Ç—å –±–∞–ª–∞–Ω—Å " : "Top-up balance in US Dollars"}
        </h2>
      </div>

      <div className="space-y-6">
        <div className="grid grid-cols-1 gap-4">
          {paymentOptions.map((option) => {
            const Icon = option.icon;
            return (
              <button
                key={option.id}
                disabled={!cpEnabled}
                className="p-4 rounded-lg border-2 border-primary bg-primary/10 glow-effect text-left opacity-90"
              >
                <div className="flex items-start gap-3">
                  <div className="w-10 h-10 rounded-lg flex items-center justify-center bg-primary/20">
                    <Icon className="w-5 h-5 text-primary" />
                  </div>
                  <div className="flex-1">
                    <p className="font-semibold text-foreground mb-1">{option.name}</p>
                    <p className="text-xs text-muted-foreground mb-2">{option.description}</p>
                    <p className="text-xs text-primary">–ö–æ–º–∏—Å—Å–∏—è: {option.fee}</p>
                  </div>
                </div>
              </button>
            );
          })}
        </div>

        <div className="space-y-2">
          <Label htmlFor="amount" className="text-foreground">
            –°—É–º–º–∞ –ø–æ–ø–æ–ª–Ω–µ–Ω–∏—è
          </Label>
          <Input
            id="amount"
            type="number"
            min="10"
            step="0.01"
            placeholder="50.00"
            value={amount}
            onChange={(e) => setAmount(e.target.value)}
            className="bg-background/50 border-border/50 focus:border-primary"
            inputMode="decimal"
          />
          <p className="text-xs text-muted-foreground">
            –ú–∏–Ω–∏–º–∞–ª—å–Ω–∞—è —Å—É–º–º–∞: $10.00 USD
          </p>
        </div>

        <div className="space-y-4 pt-4 border-t border-border/50">
          <div className="bg-muted/20 rounded-lg p-4 border border-border/30">
            <div className="grid grid-cols-1 sm:grid-cols-2 gap-2">
              <p className="text-lg font-semibold text-foreground">
                CrystalPay: {totalCrystal}
              </p>
              <p className="text-lg font-semibold text-foreground">
                LZT (5%): {totalLzt}
              </p>
            </div>
          </div>
        </div>

        <div className="grid grid-cols-1 sm:grid-cols-2 gap-3">
          <Button
            className="w-full bg-primary hover:bg-primary/90"
            disabled={!cpEnabled || !amount || Number.parseFloat(amount) < 10}
            onClick={() => handlePayment("crystalpay")}
          >
            {language === "ru" ? "–û–ø–ª–∞—Ç–∞ CrystalPay" : "Pay via CrystalPay "}
          </Button>
          <Button
            className="w-full bg-primary/80 hover:bg-primary"
            disabled={!amount || Number.parseFloat(amount) < 10}
            onClick={() => handlePayment("lzt")}
          >
            {language === "ru" ? "–û–ø–ª–∞—Ç–∞ LZT " : "Pay via LZT "}
          </Button>
        </div>
      </div>
    </div>
  );
}

export default PaymentMethods;